<template>
  <el-row :span="3">
    <el-col :span="5">
      <el-select v-model="value" :popper-append-to-body="true" class="m-2">
        <el-option
          v-for="item in migrationsForm.list"
          :key="item.random"
          :label="item.uniqueName + '/' + item.name"
          :value="item.random"
        />
      </el-select>
    </el-col>
    <el-button type="primary" @click="isShow" plain>隐藏/显示</el-button>

    <el-button type="success" @click="joinTableRow()" plain
      >添加副数据表</el-button
    >
    <el-button type="success" @click="selectRow()" plain
      >设置查询条件</el-button
    >
  </el-row>
  <el-table
    :data="fieldData"
    stripe
    style="width: 100%"
    :header-cell-style="tableHeaderColor"
  >
    <el-table-column type="selection" width="55" />
    <el-table-column label="字段名称" prop="name" width="150" align="center" />
    <el-table-column
      v-if="show"
      label="字段类型(mysql)"
      prop="type"
      width="150"
      align="center"
    />
    <el-table-column
      v-if="show"
      label="字段类型(java)"
      prop="javaType"
      width="150"
      align="center"
    />
    <el-table-column
      v-if="show"
      label="数据库默认值"
      width="150"
      prop="def"
      align="center"
    />

    <el-table-column
      v-if="show"
      label="字段大小"
      width="150"
      prop="size"
      align="center"
    >
    </el-table-column>
    <el-table-column
      v-if="show"
      label="字段描述"
      width="150"
      prop="remark"
      align="center"
    />
    <el-table-column
      v-if="show"
      label="是否为空"
      width="150"
      prop="isNull"
      align="center"
    />
    <el-table-column
      label="映射字段名称"
      width="150"
      prop="sourceName"
      align="center"
    >
    </el-table-column>
    <el-table-column
      label="映射字段类型(mysql)"
      width="190"
      prop="sourceType"
      align="center"
    >
    </el-table-column>
    <el-table-column
      label="映射字段类型(java)"
      width="190"
      prop="sourceJavaType"
      align="center"
    >
    </el-table-column>
    <el-table-column label="映射类型转换" align="center" width="170">
      <template v-slot="scope">
        <span v-show="scope.row.typeConversion == 0"> 无需转换 </span>
        <span v-show="scope.row.typeConversion == 1"> 支持 </span>
        <span v-show="scope.row.typeConversion == 2"> 不支持 </span>
      </template>
    </el-table-column>
    <el-table-column
      label="映射默认值"
      align="center"
      prop="defaulted"
      width="170"
    >
    </el-table-column>
    <el-table-column label="映射枚举" align="center"> </el-table-column>
    <el-table-column label="操作" align="center" width="150">
      <template v-slot="scope"
        ><el-button
          type="primary"
          size="small"
          @click="updateRow(scope.row)"
          plain
          >操作</el-button
        >
        <el-button
          type="danger"
          size="small"
          @click="deleteRow(scope.$index)"
          plain
          >清空</el-button
        ></template
      >
    </el-table-column>
  </el-table>
  <el-dialog
    :modelValue="update"
    title="映射字段"
    width="30%"
    @close="handleClose"
  >
    <el-form :model="updateValue" label-width="auto">
      <el-form-item v-show="updateValue.status == 1" label="id生成策略">
        <el-select v-model="idType" style="width: 80px" placeholder=" ">
          <el-option
            v-for="item in selectIdType"
            :key="item.type"
            :label="item.value"
            :value="item.type"
          >
          </el-option> </el-select
      ></el-form-item>
      <el-form-item
        v-show="updateValue.status != 1 || idType == 1"
        label="选择映射字段"
        style="width: 300px"
      >
        <el-cascader
          v-model="joinTableRef.joinTable"
          :props="joinTableOptions"
          @change="changeJoinTable($event)"
        />
      </el-form-item>

      <el-form-item
        v-show="updateValue.status != 1 || idType == 1"
        label="设置默认值"
        style="width: 300px; position: absolute"
      >
        <div v-show="updateValue.defaultValue == 2">
          <el-input v-model="updateValue.defaulted" clearable></el-input>
        </div>
        <div v-show="updateValue.defaultValue == 1">
          <el-date-picker
            v-model="updateValue.defaulted"
            type="datetime"
            format="YYYY-MM-DD hh:mm:ss"
            value-format="YYYY-MM-DD hh:mm:ss"
            placeholder="选择默认时间"
          />
        </div>

        <span v-show="updateValue.defaultValue == 0">无</span></el-form-item
      >
    </el-form>

    <template #footer>
      <span class="dialog-footer">
        <el-button
          v-show="
            joinTableRef.joinTable != null &&
            joinTableRef.joinTable.length == 2 &&
            joinTableRef.joinTable[1] != null &&
            joinTableRef.joinTable[0].name == '选择主数据表' &&
            migrationsForm.type == 2 &&
            (!timeType || joinTableRef.joinTable[1].status == 2) &&
            joinTableRef.joinTable[1].status != 1 &&
            (joinTableRef.joinTable[1].javaType == 'Long' ||
              joinTableRef.joinTable[1].javaType == 'LocalDateTime')
          "
          type="primary"
          @click="setTime"
          >{{ timeBut }}</el-button
        >
        <el-button type="primary" @click="saveRow">确认</el-button>
      </span>
    </template>
  </el-dialog>
  <el-dialog
    :modelValue="joinTable"
    title="添加副数据表"
    width="30%"
    @close="joinTableClose"
  >
    <el-form-item label="选副数据择表" style="width: 300px">
      <el-cascader
        v-model="joinTableRef.dataSources"
        :props="tableOptions"
        @change="changeTable($event, index)"
      />
    </el-form-item>
    <el-form-item label="选择join字段" style="width: 300px">
      <el-select :ref="select" v-model="joinTableRef.join" clearable>
        <el-option
          v-for="item in joinTableRef.joinFieldList"
          :key="item.name"
          :label="item.name"
          :value="item.name"
        >
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="选择查询字段" style="width: 300px">
      <el-select
        :ref="select"
        v-model="joinTableRef.query"
        :popper-append-to-body="true"
        clearable
      >
        <el-option
          v-for="item in fieldList"
          :key="item.name"
          :label="item.name"
          :value="item.name"
        >
        </el-option>
      </el-select>
    </el-form-item>
    <template #footer>
      <span class="dialog-footer">
        <el-button type="primary" @click="joinTableSave">确认</el-button>
      </span>
    </template>
  </el-dialog>
  <el-dialog
    :modelValue="tableSelect"
    title="设置查询条件"
    width="50%"
    @close="tableSelectClose"
  >
    <el-form-item label="选择表">
      <el-select
        v-model="queryName.name"
        :popper-append-to-body="true"
        clearable
      >
        <el-option
          v-for="item in joinTableList"
          :key="
            item.name == '选择主数据表'
              ? item.name
              : item.uniqueName + '/' + item.name + '/' + item.field
          "
          :label="
            item.name == '选择主数据表'
              ? item.name
              : item.uniqueName + '/' + item.name + '/' + item.field
          "
          :value="
            item.name == '选择主数据表'
              ? item.name
              : item.uniqueName + '/' + item.name + '/' + item.field
          "
        >
        </el-option>
      </el-select>
    </el-form-item>

    <el-form-item label="字段" v-for="e in queryList">
      <el-col :span="8">
        <el-select
          :ref="select"
          v-model="e.field"
          :disabled="e.query == 'last'"
          :popper-append-to-body="true"
          clearable
        >
          <el-option
            v-for="item in queryName.list"
            :key="item.name"
            :label="item.name"
            :value="item.name"
          >
          </el-option>
        </el-select>
      </el-col>
      <el-col :span="4">
        <el-select
          :ref="select"
          v-model="e.query"
          style="width: 80px"
          placeholder=" "
          clearable
        >
          <el-option
            v-for="item in query"
            :key="item"
            :label="item"
            :value="item"
          >
          </el-option>
        </el-select>
      </el-col>
      <el-col :span="1">
        <span style="color: var(--el-text-color-regular)">值</span>
      </el-col>

      <el-col :span="8">
        <el-tooltip
          v-show=""
          content="多个值用英文逗号隔开"
          placement="left"
          auto-close="2000"
          effect="light"
        >
          <span>&nbsp;</span>
        </el-tooltip>

        <el-input
          v-show="selectShow(e.field)"
          style="height: 35px; width: 200px"
          v-model="e.value"
        />

        <el-date-picker
          v-if="!selectShow(e.field)"
          v-model="e.value"
          type="datetime"
          format="YYYY-MM-DD hh:mm:ss"
          value-format="YYYY-MM-DD hh:mm:ss"
          placeholder="选择默认时间"
        />
      </el-col>

      <el-button type="primary" @click="tableSelectAdd" plain>+</el-button>
      <el-button type="danger" @click="tableSelectDelete(e)" plain>-</el-button>
    </el-form-item>

    <template #footer>
      <span class="dialog-footer">
        <el-button type="primary" @click="tableSelectSave">确认</el-button>
      </span>
    </template>
  </el-dialog>
</template>
<script setup>
import {
  defineEmits,
  defineExpose,
  onMounted,
  defineProps,
  ref,
  watch,
  reactive,
} from "vue";
import requestUtil from "@/util/request";
import { ElMessage } from "element-plus";
const databaseRef = ref(null);
const idType = ref(null);
const databaseIndexRef = ref([0]);
const tableRef = ref(null);
const timeType = ref(false);
const timeBut = ref("设置为指定时间");
const tableRefSources = ref(null);
const tableIndexRef = ref([0]);
const fieldData = ref(null);
const value = ref(null);
const queryList = ref([{ index: parseInt(Math.random() * 1000000) }]);
const queryName = ref({ name: "选择主数据表", list: [] });
const fieldList = ref([]);
const joinTableRef = ref({});
const joinTableList = ref([]);
const updateValue = ref({});
const show = ref(false);
const update = ref(false);
const joinTable = ref(false);
const tableSelect = ref(false);
const refSelect = ref([]);
let tableOptions = ref({});
let joinTableOptions = ref({});
const query = [
  "=",
  ">",
  ">=",
  "<",
  "<=",
  "!=",
  "in",
  "notIn",
  "like",
  "likeLeft",
  "likeRight",
  "isNull",
  "isNotNull",
  "notLike",
];
const selectIdType = [
  { type: 1, value: "映射" },
  { type: 2, value: "自增" },
  { type: 3, value: "雪花" },
  { type: 4, value: "uuid" },
];
const typeMethod = [
  "IntegerToLong",
  "IntegerToFloat",
  "IntegerToDouble",
  "IntegerToBoolean",
  "IntegerToString",
  "IntegerToBigDecimal",
  "LongToInteger",
  "LongToFloat",
  "LongToDouble",
  "LongToBoolean",
  "LongToString",
  "LongToBlob",
  "LongToLocalDate",
  "LongToLocalTime",
  "LongToLocalDateTime",
  "LongToByte",
  "LongToBigDecimal",
  "FloatToInteger",
  "FloatToLong",
  "FloatToDouble",
  "FloatToBoolean",
  "FloatToString",
  "DoubleToInteger",
  "DoubleToLong",
  "DoubleToFloat",
  "DoubleToBoolean",
  "BooleanToInteger",
  "BooleanToLong",
  "BooleanToFloat",
  "BooleanToDouble",
  "BooleanToString",
  "StringToInteger",
  "StringToLong",
  "StringToFloat",
  "StringToDouble",
  "StringToBoolean",
  "StringToBlob",
  "StringToClob",
  "StringToLocalDate",
  "StringToLocalTime",
  "StringToBigDecimal",
  "StringToByte",
  "StringToLocalDateTime",
  "BlobToByte",
  "BlobToString",
  "LocalDateToLong",
  "LocalDateToString",
  "LocalDateToLocalDateTime",
  "LocalTimeToString",
  "LocalTimeToLocalDateTime",
  "LocalDateTimeToLong",
  "LocalDateTimeToString",
  "LocalDateTimeToLocalDate",
  "LocalDateTimeToLocalTime",
  "BigDecimalToInteger",
  "BigDecimalToLong",
  "BigDecimalToFloat",
  "BigDecimalToDouble",
  "BigDecimalToBoolean",
  "BigDecimalToString",
  "ByteToString",
  "ByteToBlob",
];
const select = (el) => {
  if (el) {
    refSelect.value.push(el);
  }
};

const props = defineProps({
  migrationsForm: {
    type: Object,
    default: null,
    required: true,
  },
});
const getSourceData = (index, row) => {
  let isHav = false;
  if (row.sourceName == null) {
    return null;
  }
  let data = null;
  if (row.valueMap == null) {
    fieldList.value.forEach((e) => {
      if (e.name == row.sourceName) {
        data = e;
      }
    });
  } else {
    joinTableList.value.forEach((e) => {
      if (e.name == row.valueMap[1] && e.uniqueName == row.valueMap[0]) {
        e.joinFieldList.forEach((v) => {
          if (v.name == row.sourceName) {
            data = v;
          }
        });
      }
    });
  }

  if (data == null) {
    return null;
  }
  if (data.javaType == row.javaType) {
    row.typeConversion = 0;
  } else {
    const type = row.javaType == "byte[]" ? Byte : row.javaType;
    const sourceType = data.javaType == "byte[]" ? Byte : data.javaType;
    typeMethod.forEach((e) => {
      if (isHav == true) {
        return;
      }
      if (e == sourceType + "To" + type) {
        isHav = true;
      }
    });
    isHav == true ? (row.typeConversion = 1) : (row.typeConversion = 2);
  }

  row.sourceStatus = data.status;
  row.sourceRemark = data.remark;
  row.valueType = data.javaType;
  row.sourceType = data.type;
  row.sourceJavaType = data.javaType;
};

const defaultValue = (data) => {
  if (["byte[]", "Blob", "Clob"].indexOf(data.javaType) > -1) {
    return 0;
  }
  if (
    ["LocalDate", "LocalTime", "LocalDateTime", "Year"].indexOf(data.javaType) >
    -1
  ) {
    return 1;
  }
  return 2;
};

const selectShow = (v) => {
  if (v == null) {
    return true;
  }
  const data = queryName.value.list.filter((e) => e.name == v)[0];
  if (data == null) {
    return true;
  }
  let type = data.javaType;
  if (type == "LocalDateTime") {
    return false;
  }
  return true;
};
const isShow = () => {
  show.value = !show.value;
};
const setTime = () => {
  props.migrationsForm.migrationDataSources.forEach((e) => {
    if (e.migrationTables == null) {
      return;
    }
    e.migrationTables.forEach((v) => {
      if (v.random == value.value) {
        if (v.migration == false) {
          v.migrationFields.forEach((z) => {
            if (z.fieldName == joinTableRef.value.joinTable[1].name) {
              if (z.status == 0 || z.status == null) {
                z.status = 2;
                timeType.value = true;
                timeBut.value = "取消指定时间";
              } else {
                timeBut.value = "设置为指定时间";

                timeType.value = false;
                z.status = 0;
              }
              fieldList.value.forEach((x) => {
                if (x.name == z.fieldName) {
                  x.status = z.status;
                }
              });
              joinTableRef.value.joinTable[1].status = z.status;
              joinTableList.value
                .filter((e) => e.name == "选择主数据表")[0]
                .joinFieldList.forEach((x) => {
                  if (x.name == z.fieldName) {
                    x.status = z.status;
                  }
                });
            }
          });
        }
      }
    });
  });
};
const init = async (v) => {
  let data = null;
  let migrationTable = null;
  if (v == null) {
    data = props.migrationsForm.list[0];
  } else {
    data = v;
  }
  queryName.value = { name: "选择主数据表", list: [] };
  let init = data.init;
  value.value = data.random;
  if (data.migrationJoinTables != null) {
    data.migrationJoinTables.forEach((e) => {
      if (e.joinFieldList == null && e.dataSourceId != null) {
        getFieldData(e.dataSourceId, e.name, e.databaseName).then((result) => {
          e.joinFieldList = result;
        });
      }
    });
    joinTableList.value = data.migrationJoinTables;
  } else {
    joinTableList.value = [];
  }
  fieldData.value = await getFieldData(
    data.sourceId,
    data.name,
    data.databaseName
  );
  fieldList.value = await getFieldData(
    data.sourceIds,
    data.sourceName,
    data.sourceDatabaseName
  );

  if (joinTableList.value.filter((e) => e.name == "选择主数据表") == 0) {
    joinTableList.value.push({
      name: "选择主数据表",
      uniqueName: "1",
      field: "1",
      joinFieldList: fieldList.value,
    });
  }
  props.migrationsForm.migrationDataSources.forEach((e) => {
    if (e.migrationTables == null) {
      return;
    }
    e.migrationTables.forEach((v) => {
      if (v.random == value.value) {
        if (v.migration == true) {
          if (v.idType == null) {
            v.idType = 1;
          }
          idType.value = v.idType;
          migrationTable = v;
        } else {
          v.migrationFields.forEach((z) => {
            fieldList.value.forEach((x) => {
              if (x.name == z.fieldName) {
                x.status = z.status;
              }
            });
            if (z.status == 2) {
              timeType.value = true;
              timeBut.value = "取消指定时间";
            }
          });
        }
      }
    });
  });

  fieldData.value.forEach((e, index) => {
    e.defaultValue = defaultValue(e);
    const filed = migrationTable.migrationFields.filter((v) => {
      return v.fieldName == e.name;
    });
    if (filed[0].value == null || filed[0].value.trim().length == 0) {
      const data = fieldList.value.filter((v) => {
        return v.name == e.name;
      });
      if (
        data.length > 0 &&
        init != true &&
        (e.status != 1 || idType.value == 1)
      ) {
        e.sourceName = e.name;
      }
    } else {
      e.sourceName = filed[0].value;
    }

    e.defaulted = filed[0].defaulted;
    e.valueMap = filed[0].valueMap;
  });
  data.init = true;
  getJoinTableOptions();
  tableOptions.value = {
    lazy: true,
    lazyLoad(node, resolve) {
      const { level } = node;
      setTimeout(() => {
        let data = null;
        if (level == 1) {
          // 等于1代表二级目录 获取数据库下所有库
          const value = node.value.split(";");
          data = getTable(value[1], value[0]);
          data.then((result) => {
            if (result == null) {
              resolve([]);
            }
            const nodes = result.map((item) => ({
              value: item,
              label: item,
              leaf: level >= 1,
            }));

            // 通过调用resolve将子节点数据返回，通知组件数据加载完成
            resolve(nodes);
          });
        } else {
          // 根目录获取所有数据库配置
          data = props.migrationsForm.migrationDataSources;
          if (data == null) {
            resolve([]);
          }
          data = data.filter((e) => JSON.stringify(e) != "{}");
          if (data == null) {
            resolve([]);
          }
          const nodes = data.map((item, index) => ({
            value:
              item.databaseName +
              ";" +
              item.sourceId +
              ";" +
              item.uniqueName +
              ";" +
              item.type +
              ";" +
              index,
            label: item.databaseName,
            leaf: level >= 1,
          }));

          if (nodes.length == 0) {
            resolve([]);
          }
          // 通过调用resolve将子节点数据返回，通知组件数据加载完成
          resolve(nodes);
        }
      }, 10);
    },
  };
};
const getFieldData = async (id, talbeName, databaseName) => {
  const res = await requestUtil.get(
    "admin_api/v1/dataBase/queryField/" +
      id +
      "?" +
      "talbeName=" +
      talbeName +
      "&databaseName=" +
      databaseName
  );
  return res.data.data;
};
const getTable = async (id, dataName) => {
  const res = await requestUtil.get(
    "admin_api/v1/dataBase//queryTable/" + id + "?dataName=" + dataName
  );
  return res.data.data;
};
const tableHeaderColor = ({ row, column, rowIndex, columnIndex }) => {
  if (show.value) {
    if (rowIndex === 0 && columnIndex > 7) {
      return { background: "#eef1f6", color: "#3e3f42" };
    }
    if (rowIndex === 0 && columnIndex <= 7) {
      return { background: "#eef1f6" };
    }
  } else {
    if (rowIndex === 0 && columnIndex > 1) {
      return { background: "#eef1f6", color: "#3e3f42" };
    }
    if (rowIndex === 0 && columnIndex <= 1) {
      return { background: "#eef1f6" };
    }
  }
};
const getJoinTableOptions = () => {
  joinTableOptions.value = {
    lazy: true,
    lazyLoad(node, resolve) {
      const { level } = node;
      setTimeout(() => {
        let data = null;
        if (level == 1) {
          // 等于1代表二级目录 获取数据库下所有库
          let value = node.value;
          data = joinTableList.value.filter(
            (e) =>
              e.uniqueName == value.uniqueName &&
              e.name == value.name &&
              e.field == value.field
          );
          if (data == null) {
            resolve([]);
          }
          const nodes = data[0].joinFieldList.map((item) => ({
            value: item,
            label: item.name,
            leaf: level >= 1,
          }));

          // 通过调用resolve将子节点数据返回，通知组件数据加载完成
          resolve(nodes);
        } else {
          // 根目录获取所有数据库配置
          const nodes = joinTableList.value.map((item, index) => ({
            value: item,
            label:
              item.name == "选择主数据表"
                ? item.name
                : item.uniqueName + "/" + item.name + "/" + item.field,
            leaf: level >= 1,
          }));

          if (nodes.length == 0) {
            resolve([]);
          }
          // 通过调用resolve将子节点数据返回，通知组件数据加载完成
          resolve(nodes);
        }
      }, 10);
    },
  };
};
const VisibleChange = () => {
  refSelect.value.forEach((bkpRef) => {
    if (bkpRef.toggleDropDownVisible) {
      bkpRef.toggleDropDownVisible(false);
    } else {
      bkpRef.visible = false;
    }
  });
};
const deleteRow = (index) => {
  if (fieldData.value[index].valueMap != null) {
    props.migrationsForm.migrationDataSources.forEach((e) => {
      if (e.migrationTables == null) {
        return;
      }
      e.migrationTables = e.migrationTables.map((v) => {
        if (v.random == value.value) {
          if (v.migration == true) {
            v.migrationFields = v.migrationFields.map((x) => {
              if (x.fieldName == fieldData.value[index].name) {
                x.valueMap = null;
              }
              return x;
            });
            if (v.migrationJoinTables != null) {
              v.migrationJoinTables = v.migrationJoinTables.map((x) => {
                if (
                  x.joinFiled != fieldData.value[index].sourceName &&
                  x.name == fieldData.value[index].valueMap[1] &&
                  x.uniqueName == fieldData.value[index].valueMap[0] &&
                  x.field == fieldData.value[index].valueMap[2]
                ) {
                  x.selectList = x.selectList.filter(
                    (z) => z != fieldData.value[index].sourceName
                  );
                }
                return x;
              });
            }
          }
        }
        return v;
      });
    });
  }
  fieldData.value[index].defaulted = "";
  fieldData.value[index].sourceName = "";
  fieldData.value[index].sourceType = null;
  fieldData.value[index].valueType = null;
  fieldData.value[index].valueMap = null;
  fieldData.value[index].sourceJavaType = null;
  fieldData.value[index].typeConversion = null;
};
const updateRow = (data) => {
  let table = null;
  updateValue.value = data;
  if (data.sourceName != null) {
    if (data.valueMap == null) {
      table = joinTableList.value.filter((e) => e.name == "选择主数据表");
    } else {
      table = joinTableList.value.filter(
        (e) =>
          e.uniqueName == data.valueMap[0] &&
          e.name == data.valueMap[1] &&
          e.field == data.valueMap[2]
      );
    }
    joinTableRef.value.joinTable = [
      table[0],
      table[0].joinFieldList.filter((e) => e.name == data.sourceName)[0],
    ];
  } else {
    joinTableRef.value.joinTable = [];
  }

  update.value = true;
};

const joinTableClose = () => {
  joinTable.value = false;
  joinTableRef.value = {};
};
const tableSelectClose = () => {
  tableSelect.value = false;
};
const tableSelectDelete = (e) => {
  queryList.value = queryList.value.filter((v) => v.index != e.index);
};

const tableSelectAdd = () => {
  queryList.value.push({ index: parseInt(Math.random() * 1000000) });
};
const tableSelectSave = () => {
  let error = false;
  let map = {};
  let migrationTable = null;
  let tables = null;
  queryList.value.forEach((e) => {
    let queryValue = null;
    if (e.query == "=") {
      queryValue = "eq";
    } else if (e.query == ">") {
      queryValue = "gt";
    } else if (e.query == ">=") {
      queryValue = "ge";
    } else if (e.query == "<") {
      queryValue = "lt";
    } else if (e.query == "<=") {
      queryValue = "le";
    } else if (e.query == "!=") {
      queryValue = "ne";
    } else {
      queryValue = e.query;
    }
    map[e.field + "_&" + queryValue] = e.value;
    if (
      error == false &&
      (e.query == null || e.field == null || e.value == null)
    ) {
      ElMessage.error("参数不能为空！");
      error = true;
      return;
    }
  });
  if (error) {
    return;
  }
  props.migrationsForm.migrationDataSources.forEach((e) => {
    if (e.migrationTables == null) {
      return;
    }
    e.migrationTables.forEach((v) => {
      if (v.random == value.value) {
        if (v.migration == true) {
          migrationTable = v;
        } else {
          tables = v;
        }
      }
    });
  });
  if (queryName.value.name == "选择主数据表") {
    migrationTable.queryMap = map;
    queryList.value.forEach((e) => {
      if (e.field == null || e.field == "") {
        return;
      }
      const data = tables.migrationFields.filter((v) => v.fieldName == e.field);
      if (data.length == 0) {
        const fileds = queryName.value.list.filter((v) => v.name == e.field)[0];
        tables.migrationFields.push({
          fieldName: fileds.name,
          fieldType: fileds.type,
          remark: fileds.def,
          status: fileds.status,
        });
      }
    });
  } else {
    const data = queryName.value.name.split("/");
    migrationTable.migrationJoinTables.forEach((e) => {
      if (e.uniqueName == data[0] && e.name == data[1] && e.field == data[2]) {
        e.queryMap = map;
      }
    });
    props.migrationsForm.migrationDataSources.forEach((e) => {
      if (e.migrationTables == null) {
        return;
      }
      if (e.uniqueName != data[0]) {
        return;
      }
      e.migrationTables.forEach((v) => {
        if (v.name == data[1]) {
          queryList.value.forEach((z) => {
            if (z.field == null || z.field == "") {
              return;
            }
            const data = v.migrationFields.filter(
              (x) => x.fieldName == z.field
            );
            if (data.length == 0) {
              const fileds = queryName.value.list.filter(
                (x) => x.name == z.field
              )[0];
              v.migrationFields.push({
                fieldName: fileds.name,
                fieldType: fileds.type,
                remark: fileds.def,
                status: fileds.status,
              });
            }
          });
        }
      });
    });
  }
  tableSelect.value = false;
};
const joinTableSave = () => {
  if (
    joinTableRef.value.dataSources == null ||
    joinTableRef.value.query == null ||
    joinTableRef.value.joinFieldList == null
  ) {
    ElMessage.error("参数不能为空！");
    return;
  }
  joinTable.value = false;
  let migrationTable = null;
  let add = true;
  let tableName = joinTableRef.value.dataSources[1];
  let tableUniqueName = joinTableRef.value.dataSources[0].split(";")[2];
  /**
   * 添加表和join 字段
   */
  const data = joinTableRef.value.joinFieldList.filter(
    (e) => e.name == joinTableRef.value.join
  );
  let dataFied = {
    fieldName: data[0].name,
    fieldType: data[0].type,
    remark: data[0].remark,
    status: data[0].status,
  };

  const joinData = fieldList.value.filter(
    (e) => e.name == joinTableRef.value.query
  );
  let joinDataFied = {
    fieldName: joinData[0].name,
    fieldType: joinData[0].type,
    remark: joinData[0].remark,
    status: joinData[0].status,
  };
  props.migrationsForm.migrationDataSources.forEach((e) => {
    if (e.migrationTables == null) {
      return;
    }
    e.migrationTables.forEach((v) => {
      if (v.random == value.value) {
        if (v.migration == true) {
          migrationTable = v;
        } else {
          if (
            v.migrationFields.filter(
              (x) => x.fieldName == joinTableRef.value.query
            ).length == 0
          ) {
            v.migrationFields.push(joinDataFied);
          }
        }
      }
      if (e.uniqueName == tableUniqueName && v.name == tableName) {
        add = false;
        if (
          v.migrationFields.filter(
            (x) => x.fieldName == joinTableRef.value.join
          ).length == 0
        ) {
          v.migrationFields.push(dataFied);
        }
      }
    });
    if (add && e.uniqueName == tableUniqueName) {
      e.migrationTables.push({
        migration: false,
        name: tableName,
        type: e.type,
        migrationFields: [dataFied],
      });
    }
  });

  if (!migrationTable.migrationJoinTables) {
    migrationTable.migrationJoinTables = [];
  }
  let jointableData = {
    field: joinTableRef.value.query,
    fieldType: joinData[0].javaType,
    joinFiled: joinTableRef.value.join,
    selectList: [joinTableRef.value.join],
    joinFiledType: data[0].javaType,
    joinFieldList: joinTableRef.value.joinFieldList,
    name: tableName,
    uniqueName: tableUniqueName,
  };
  let tableData = migrationTable.migrationJoinTables.filter(
    (e) =>
      e.name == jointableData.name &&
      e.uniqueName == jointableData.uniqueName &&
      e.field == jointableData.field
  );
  if (tableData.length == 0) {
    migrationTable.migrationJoinTables.push(jointableData);
  } else {
    migrationTable.migrationJoinTables = migrationTable.migrationJoinTables.map(
      (e) => {
        if (
          e.name == jointableData.name &&
          e.uniqueName == jointableData.uniqueName &&
          e.field == jointableData.field
        ) {
          e.selectList.forEach((v) => {
            if (v == e.joinFiled) {
              v = jointableData.joinFiled;
            }
          });
        }
        e.joinFiled = jointableData.joinFiled;
        e.joinFiledType = jointableData.joinFiledType;

        return e;
      }
    );
  }
  joinTableList.value = migrationTable.migrationJoinTables;
  if (joinTableList.value.filter((e) => e.name == "选择主数据表") == 0) {
    joinTableList.value.push({
      name: "选择主数据表",
      uniqueName: "1",
      field: "1",
      joinFieldList: fieldList.value,
    });
  }
  getJoinTableOptions();
  joinTableRef.value = {};
};
const joinTableRow = () => {
  joinTable.value = true;
};
const selectRow = () => {
  tableSelect.value = true;
  queryInit();
};
const saveRow = () => {
  update.value = false;
};
const handleClose = () => {
  update.value = false;
};
const changeTable = async (value, index) => {
  const data = value[0].split(";");
  joinTableRef.value.joinFieldList = await getFieldData(
    data[1],
    value[1],
    data[0]
  );
};
const changeJoinTable = (values) => {
  // 添加字段  添加valuemap 回显 seclet字段
  const parent = values[0];
  const child = values[1];
  const oldValueMap = updateValue.value.valueMap;
  const oldSourceName = updateValue.value.sourceName;
  let migrationTables = null;
  props.migrationsForm.migrationDataSources.forEach((e) => {
    if (e.migrationTables == null) {
      return;
    }
    e.migrationTables.forEach((v) => {
      if (v.random == value.value && v.migration == true) {
        migrationTables = v;
      }
    });
  });
  if (parent.name != "选择主数据表") {
    updateValue.value.valueMap = [parent.uniqueName, parent.name, parent.field];
  } else {
    updateValue.value.valueMap = null;
  }
  if (migrationTables.migrationFields != null) {
    migrationTables.migrationFields.forEach((e) => {
      if (e.fieldName == updateValue.value.name) {
        e.valueMap =
          parent.name != "选择主数据表"
            ? [parent.uniqueName, parent.name, parent.field]
            : null;
      }
    });
  }
  updateValue.value.sourceName = child.name;
  if (parent.name != "选择主数据表") {
    props.migrationsForm.migrationDataSources.forEach((e) => {
      if (e.migrationTables == null) {
        return;
      }
      e.migrationTables.forEach((v) => {
        if (e.uniqueName == parent.uniqueName && v.name == parent.name) {
          if (
            v.migrationFields.filter((x) => x.fieldName == child.name).length ==
            0
          ) {
            v.migrationFields.push({
              fieldName: child.name,
              fieldType: child.type,
              remark: child.remark,
              status: child.status,
            });
          }
        }

        if (v.migrationJoinTables != null) {
          v.migrationJoinTables.forEach((x) => {
            if (
              x.uniqueName == parent.uniqueName &&
              x.name == parent.name &&
              x.field == parent.field
            ) {
              if (x.selectList.filter((z) => z == child.name).length == 0) {
                x.selectList.push(child.name);
              }
            }
            if (
              oldValueMap != null &&
              x.name == oldValueMap[1] &&
              x.uniqueName == oldValueMap[0] &&
              x.field == oldValueMap[2] &&
              x.joinFiled != oldSourceName &&
              v.migrationFields.filter(
                (z) =>
                  z.valueMap != null &&
                  z.valueMap.join(", ") == oldValueMap.join(", ") &&
                  z.value == oldSourceName
              ).length < 2
            ) {
              x.selectList = x.selectList.filter((z) => z != oldSourceName);
            }
          });
        }
      });
    });
  }
};
const queryInit = () => {
  queryList.value = [];
  let map = null;
  let migrationTable = null;
  props.migrationsForm.migrationDataSources.forEach((e) => {
    if (e.migrationTables == null) {
      return;
    }
    e.migrationTables.forEach((e) => {
      if (e.random == value.value) {
        if (e.migration == true) {
          migrationTable = e;
        } else {
        }
      }
    });
  });
  if (queryName.value.name == "选择主数据表") {
    queryName.value.list = fieldList.value;

    map = migrationTable.queryMap;
  } else {
    const data = queryName.value.name.split("/");
    migrationTable.migrationJoinTables.forEach((e) => {
      if (e.uniqueName == data[0] && e.name == data[1] && e.field == data[2]) {
        map = e.queryMap;
        queryName.value.list = e.joinFieldList;
      }
    });
  }
  if (map != null && JSON.stringify(map) != "{}") {
    for (let e in map) {
      const split = e.split("_&");
      let queryValue = null;
      if (split[1] == "eq") {
        queryValue = "=";
      } else if (split[1] == "gt") {
        queryValue = ">";
      } else if (split[1] == "ge") {
        queryValue = ">=";
      } else if (split[1] == "lt") {
        queryValue = "<";
      } else if (split[1] == "le") {
        queryValue = "<=";
      } else if (split[1] == "ne") {
        queryValue = "!=";
      } else {
        queryValue = split[1];
      }
      let v = map[e];
      queryList.value.push({
        value: v,
        index: parseInt(Math.random() * 1000000),
        query: queryValue,
        field: split[0],
      });
    }
  } else {
    queryList.value = [{}];
  }
};
watch(
  value,
  (newValue, oldValue) => {
    const data = props.migrationsForm.list.filter(
      (e) => e.random == newValue
    )[0];
    init(data);
  },
  { deep: true }
);
watch(
  queryList,
  (newValue, oldValue) => {
    queryList.value.forEach((e) => {
      if (e.query == "last") {
        e.field = "";
      }
    });
  },
  { deep: true }
);
watch(
  queryName,
  (newValue, oldValue) => {
    queryInit();
  },
  { deep: true }
);
watch(
  idType,
  (newValue, oldValue) => {
    if (idType.value > 1) {
      fieldData.value.forEach((e, index) => {
        if (e.status == 1) {
          deleteRow(index);
        }
      });
    }
    props.migrationsForm.migrationDataSources.forEach((e) => {
      if (e.migrationTables == null) {
        return;
      }
      e.migrationTables.forEach((v) => {
        if (v.random == value.value) {
          if (v.migration == true) {
            v.idType = idType.value;
          }
        }
      });
    });
  },
  { deep: true }
);
watch(
  fieldData,
  (newValue, oldValue) => {
    fieldData.value.forEach((e, index) => {
      getSourceData(index, e);
    });
    props.migrationsForm.migrationDataSources.forEach((e) => {
      if (e.migrationTables == null) {
        return;
      }
      e.migrationTables.forEach((e) => {
        if (e.random == value.value) {
          if (!e.migrationFields) {
            e.migrationFields = [];
          }
          fieldData.value.forEach((v) => {
            let update = false;
            let migrationField = null;
            if (e.migration == true) {
              migrationField = {
                fieldName: v.name,
                fieldType: v.type,
                valueType: v.valueType,
                value: v.sourceName,
                valueMap: v.valueMap,
                defaulted: v.defaulted,
                remark: v.remark,
                status: v.status,
              };
            } else {
              if (
                v.sourceName == null ||
                v.sourceName.length == 0 ||
                v.valueMap != null
              ) {
                return;
              }
              migrationField = {
                fieldName: v.sourceName,
                fieldType: v.sourceType,
                remark: v.sourceRemark,
                status: v.sourceStatus,
              };
            }

            e.migrationFields = e.migrationFields.map((x) => {
              if (!x) {
                return x;
              }
              if (
                x.fieldName == v.name &&
                migrationField.fieldName != null &&
                migrationField.fieldName != undefined
              ) {
                if (
                  migrationField.value == null ||
                  migrationField.value == undefined
                ) {
                  migrationField.value = x.value;
                }
                if (
                  migrationField.defaulted == null ||
                  migrationField.defaulted == undefined
                ) {
                  migrationField.defaulted = x.defaulted;
                }
                migrationField.id = x.id;
                if (x.valueMap != null) {
                  migrationField.valueMap = x.valueMap;
                }
                if (x.status != null) {
                  migrationField.status = x.status;
                }
                x = migrationField;
                update = true;
              }
              return x;
            });
            if (
              update == false &&
              migrationField.fieldName != null &&
              migrationField.fieldName != undefined
            ) {
              if (
                e.migrationFields.filter(
                  (e) => e.fieldName == migrationField.fieldName
                ).length == 0
              ) {
                e.migrationFields.push(migrationField);
              }
            }
          });
        }
      });
    });
  },
  { deep: true }
);

defineExpose({
  init,
});
</script>
<style scoped>
.m-2 {
  height: 60px;
}
.el-form-item {
  margin-bottom: 25px;
}
.selectTable {
  font-size: 14px;
  font-weight: 400;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding-top: 4px;
  border-top: 1px solid rgb(240 242 245);
}
</style>
